.ContainerForm {
	box-shadow: 0px 2px 2px 2px rgba(2, 2, 2, 0.2);
    max-width: 370px;
    margin: 0 auto;
    padding: 15px;
    margin-bottom: 30px;
	display: flex;
	flex-direction: column;
}

.Title {
	margin: 0;
  margin-bottom: 15px;
  font-size: 40px;
  color: rgb(0, 0, 0);
}

label {
    display: flex;
    align-items: center;
    padding: 5px 15px;   
	margin-bottom: 15px; 
    background: #fff;
    border-radius: 5px;
    box-shadow: 0px 2px 2px 2px rgba(2, 2, 2, 0.2);
	list-style-type: none;
	font-size: 24px;
  	color: rgb(0, 0, 0);
}



input {
	padding-left: 10px;
	width: 100%;
	height: 20px;
	margin-left: 15px;
	border: 2px solid #000;
	border-radius: 5px;
	outline: none;

}

input:focus,
input:hover {
	cursor: pointer;
	border: 2px solid rgb(17, 0, 255);
}

.Btn {
	margin-top: 20px;
	width: 60px;
 	background: #fff;
    border-radius: 5px;
    box-shadow: 0px 1px 1px 1px rgba(2, 2, 2, 0.2);
	list-style-type: none;
	font-size: 14px;
  	color: rgb(0, 0, 0);
	outline: none;
}

.Btn:hover,
.Btn:focus {
	cursor: pointer;
	border: 2px solid rgb(17, 0, 255);	
}


